import * as React from 'react';
import anime from 'animejs';

interface ShakeDiceProps {
  source:any;
  delay:number;
  style?:React.CSSProperties;
}

interface ShakeDiceState {}

export class ShakeDice extends React.Component<ShakeDiceProps, ShakeDiceState > {
  protected img_ref:React.RefObject<any>;
  protected anime_timeline:any;
  
  public constructor(props:ShakeDiceProps) {
    super(props);
    this.img_ref = React.createRef();
    this.anime_timeline = anime.timeline({
      easing: 'easeOutExpo',
      loop: true
    });
  }
  
  public componentDidMount() {
    setTimeout(() => {
      this.anime_timeline.add({
        targets:this.img_ref.current,
        duration:300,
        rotate:[0, 360],
      });
    }, this.props.delay);
  }
  
  public render(){
    const { style } = this.props;
    const _style = {
      position:'absolute' as 'absolute',
      width:48,
      height:51,
      bottom:90,
      right:75,
      ...style,
    };
    return (
      <img
        style={_style}
        ref={this.img_ref}
        src={this.props.source}
      />
    );
  }
}



