import * as React from 'react';
import anime from 'animejs';
import { FormattedMessage as F } from 'react-intl';
import bucketImg from 'src/assets/bucket.png';
import shakeDiceImg1 from 'src/assets/shake_dices/shake_big_1.png';
import shakeDiceImg2 from 'src/assets/shake_dices/shake_big_2.png';
import shakeDiceImg3 from 'src/assets/shake_dices/shake_big_3.png';
import resultDice1 from 'src/assets/result_dices/dice_11@3x.png';
import resultDice2 from 'src/assets/result_dices/dice_21@3x.png';
import resultDice3 from 'src/assets/result_dices/dice_31@3x.png';
import resultDice4 from 'src/assets/result_dices/dice_41@3x.png';
import resultDice5 from 'src/assets/result_dices/dice_51@3x.png';
import resultDice6 from 'src/assets/result_dices/dice_61@3x.png';
import { ResultDice } from './result_dice';
import popup_mask from 'src/assets/tablecloth/table_mask.png';

const result_dices = [
  resultDice1,
  resultDice2,
  resultDice3,
  resultDice4,
  resultDice5,
  resultDice6,
];

import { ShakeDice } from './shake_dice';

interface DiceBucketProps {
  result:number[];
  finished:boolean;
  on_click?:() => void;
}

interface DiceBucketState {
}

class ThreeDice extends React.Component{
  protected div_ref:React.RefObject<any>;
  
  public constructor(props:{}) {
    super(props);
    this.div_ref = React.createRef();
  }
  
  public componentDidMount() {
    anime({
      targets:this.div_ref.current,
      rotate:[0,360],
      duration:100,
      loop:true,
    });
  }
  
  public render() {
    return (
      <div ref={this.div_ref} className='three_dice'>
        <ShakeDice style={{
          position:'absolute',
          top:-20,
          left:25,
        }} delay={0} source={shakeDiceImg1}/>
        <ShakeDice style={{
          position:'absolute',
          top:50,
          left:-10,
        }} delay={0} source={shakeDiceImg2}/>
        <ShakeDice style={{
          position:'absolute',
          top:50,
          left:55
        }} delay={0} source={shakeDiceImg3}/>
      </div>
    )
  }
}

export class DiceBucket extends React.Component<DiceBucketProps, DiceBucketState > {
  public constructor(props:DiceBucketProps) {
    super(props);
    this.render_result_dices = this.render_result_dices.bind(this);
    this.render_result_tips = this.render_result_tips.bind(this);
  }
  public render(){
    const { finished, result } = this.props;
    return (<div
      className='dice_bucket'
      onClick={this.props.on_click} style={{
      position:'absolute',
      top:113,
      left:78
    }}>
      <img style={{width:211,height:279}} src={bucketImg}/>
      { finished && <ResultDice number={result[0]} style={{bottom:100, left:75}}/>}
      { finished && <ResultDice number={result[1]} style={{bottom:80, left:50}}/> }
      { finished && <ResultDice number={result[2]} style={{bottom:80, left:110}}/> }
      {/*{ !finished && <ShakeDice delay={0} source={shakeDiceImg1}/>}*/}
      {/*{ !finished && <ShakeDice delay={50} source={shakeDiceImg2}/>}*/}
      {/*{ !finished && <ShakeDice delay={100} source={shakeDiceImg3}/>}*/}
      { !finished && <ThreeDice/> }
      { finished && this.render_result_tips() }
      { finished && this.render_result_dices() }
    </div>);
  }
  
  private render_result_tips() {
    const total = this.props.result.reduce((total_result, item) => total_result += item, 0) as number;
    return (
      <div className='result_text'>
        <text className='total'>{ total }</text>
        <F tagName='span' id={total > 9 ? 'result_big' : 'result_small'}/>
      </div>
    );
  }
  
  private render_result_dices() {
    const { result } = this.props;
    const icon = {
      width:47,
      height:47,
      marginRight:37,
    };
    return (
      <div style={{position:'absolute', display:'flex', justifyContent:'center', alignItems:'center'}}>
        <img style={icon} src={result_dices[result[0]-1]} alt=""/>
        <img style={icon} src={result_dices[result[1]-1]} alt=""/>
        <img style={icon} src={result_dices[result[2]-1]} alt=""/>
      </div>
    );
  }
}



